<template>
  <div>
    <input type="text" v-model="neTest">
    <el-image :src='img.imgList.url' lazy></el-image>
    <i-input v-model="ip"></i-input>
  </div>
</template>
<script>
import iInput from './input/input'
export default {
  name: 'comps',
  data(){
    return {
      neTest: this.test,
      ip: ''
    }
  },
  components: {
    iInput
  },
  props: {
    obj: {
      type: Object,
      default: () => {
        return {}
      }
    },
    someObject: {
      type: Object,
      default: () => {
        return {}
      }
    },
    day: {
      type: Array,
      default: () => {
        return []
      }
    },
    test: {
      type: String,
      default: ''
    },
    img: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  watch: {
    test(now){
      this.neTest = now
    }
  }
};
</script>