<template>
  <div id="app">
    <Upload></Upload>
    <!-- <input type="text" v-model="test"> -->
    <!-- <img alt="Vue logo" src="./assets/logo.png" /> -->
    <!-- <HelloWorld msg="Welcome to Your Vue.js App" /> -->
    <!-- <test :test='test' :img='img' :obj='obj' :day='day' :someObject='someObject'></test> -->
    <!-- <A></A> -->
    <!-- <el-button @click="testAlert">one</el-button> -->
    <!-- <check-box v-model="cbox" trueValue='0' falseValue='1'>测试BOX</check-box>
    <div @click="wt">{{cbox}}</div>-->

    <!-- <div id="container"></div>
    <div id="panel"></div>
    <el-button @click="initMap" size="mini">创建</el-button>
    <el-button @click="destroyMap" size="mini">销毁</el-button>
    <el-image :src='im'></el-image>
    <ls-tables></ls-tables> -->
    <!-- <div class="infinite-list" v-infinite-scroll="load" style="overflow:auto;height: 80px;">
      <div>
        <div v-for="i in count" class="infinite-list-item" :key="i">{{ i }}</div>
      </div>
    </div> -->
  </div>
</template>

<script>
import lsVideo from './components/lsVideo'
import im from './assets/applyForBanner.jpg'
import lsTables from "./view/table-render";
import HelloWorld from "./components/HelloWorld.vue";
import Test from "./components/test.vue";
import A from "./components/compA.vue";
import checkBox from "./components/checkbox/checkbox";
import unit from "./components/unit.vue";
import IInput from "./components/input/input";
import Upload from './components/upload/upload'
import {
  findComponentUpward,
  findComponentDownward,
  findComponentsDownward
} from "./utils/assist";
export default {
  provide() {
    return {
      lApp: this
    };
  },
  data() {
    return {
      im: im,
      count: 0,
      cbox: false,
      test: "test msg",
      img: {
        imgList: {
          url:
            "https://sun-1253313441.cos.ap-chengdu.myqcloud.com/images/1569398263222"
        }
      },
      obj: { arr: [{ name: "asdas" }] },
      day: [{ p: "pp" }],
      _test: "_test",
      someObject: { a: "a" },
      firstInitMap: {
        // 是否可以旋转
        rotateEnable: true,
        // 是否可以抖斜
        pitchEnable: true,
        //
        resizeEnable: true,
        // zoom: 11,
        // Y轴的角度
        pitch: 65,
        center: [116.397428, 39.90923],
        // 开启3D视图,默认为关闭
        viewMode: "3D",
        // 属于X轴旋转
        rotation: "-15",
        // true表示可以缩放到最小值，false就不能 是否可以缩放到20的等级
        expandZoomRange: true,
        // 楼块出现是否带动画
        buildingAnimation: true,
        zooms: [13, 20]
        // lang: 'en'
      },
      map: null,
      scale: null,
      toolBar: null,
      controlBar: null,
      district: null,
      driving: null,
      // 下四个表示 区域面，道路，建筑物，标注
      features: ["bg", "road", "building", "point"],
      opts: {
        subdistrict: 0,
        extensions: "all",
        level: "city"
      }
    };
  },
  created() {
    // this.$nextTick(() => {
    //   this.initMap();
    //   this.initLoadEnd();
    // });
  },
  mounted() {
    // const find = findComponentDownward(this, "comp");
    // const find = findComponentsDownward(this, "comp");
    // console.log(find, "findComponentDownward");
    // console.log(this.testss(), "testss");
  },
  methods: {
    load() {
      console.log('load')
      this.count += 2;
    },
    testAlert() {
      this.$LAlert({
        content: "this is info box",
        duration: 3
      });
    },
    initMap() {
      // map
      this.map = new AMap.Map("container", this.firstInitMap);
      // scale尺子
      this.scale = new AMap.Scale({ visible: false });
      // var path = [];
      // path.push([116.303843, 39.983412]);
      // path.push([116.321354, 39.896436]);
      // path.push([116.407012, 39.992093]);
      // this.dragRoute = new AMap.DragRoute(this.map, path, AMap.DrivingPolicy.LEAST_FEE)
      // this.dragRoute.search()
      // map缩放工具
      this.toolBar = new AMap.ToolBar({ visible: false });
      // 3d都斜工具
      this.controlBar = new AMap.ControlBar({
        showZoomBar: false,
        showControlButton: true,
        position: { right: "10px", top: "10px" }
      });
      // 区域掩饰
      this.district = new AMap.DistrictSearch(this.opts);
      // 路线规划
      this.driving = new AMap.Driving({
        map: this.map,
        panel: "panel"
      });
      // 驾车信息
      this.drivingFn();
      // 拖拽改变线路
      this.drag();
      // map添加控件
      this.map.addControl(this.scale);
      this.map.addControl(this.toolBar);
      this.map.addControl(this.controlBar);
      // map设置地图标记等
      this.map.setFeatures(this.features);
      this.scale.show();
      this.toolBar.show();
    },
    drivingFn() {
      this.driving.search(
        new AMap.LngLat(116.379028, 39.865042),
        new AMap.LngLat(116.427281, 39.903719),
        (status, result) => {
          // result 即是对应的驾车导航信息，相关数据结构文档请参考  https://lbs.amap.com/api/javascript-api/reference/route-search#m_DrivingResult'
          console.log(status, result, "status, result");
          if (status === "complete") {
            console.log("绘制驾车路线完成");
          } else {
            console.log("获取驾车数据失败：" + result);
          }
        }
      );
    },
    drag() {
      var path = [];
      var route;
      path.push([116.303843, 39.983412]);
      //path.push([116.321354, 39.896436]);
      path.push([116.407012, 39.992093]);
      // js插件写法
      this.map.plugin("AMap.DragRoute", () => {
        route = new AMap.DragRoute(
          this.map,
          path,
          AMap.DrivingPolicy.LEAST_FEE
        ); //构造拖拽导航类
        route.search(); //查询导航路径并开启拖拽导航
      });
      console.log(AMap.DrivingPolicy.LEAST_FEE, "AMap.DrivingPolicy.LEAST_FEE");
    },
    initLoadEnd() {
      this.map &&
        this.map.on("complete", function() {
          // window.alert("地图加载完成！");
        });
    },
    // 销毁
    destroyMap() {
      this.map && this.map.destroy();
    },
    wt() {
      this.cbox = !this.cbox;
    },
    testss() {
      var arr = [];
      return arr.reduce((per, item) => {
        console.log("for in");
        return item;
      }, []);
    }
  },
  name: "comp",
  components: {
    Upload,
    HelloWorld,
    Test,
    A,
    checkBox,
    unit,
    IInput,
    lsTables,
    lsVideo
  }
};
</script>

