// 认证页面
import axios from '../axios'
var qs = require('qs')
// 身份认证页-根据参赛编号获取就读年级
/**
 *
 * @param {number} data 参数编号 一般为手机号
 */
const gradeName = (data) => {
  return axios({
    url: '/custom/owens/grade-name',
    method: 'get',
    params: data
  })
}
// 身份认证页-提交报名
/**
 *
 * @param {Content-Type} Headers： application/x-www-form-urlencoded
 * @param {Body} data name number > string
 */
const applySubmit = data => {
  return axios({
    url: '/custom/owens/offline/apply',
    method: 'post',
    data: qs.stringify(data),
    headers: {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
  })
}
export default { gradeName, applySubmit }
