
import axios from 'axios'
// import { Message } from 'element-ui'
import { getCookies, rmCookies, setCookies } from '@/tools/util'
import { MockUrl } from './config'
// import router from '../router/index'
// 创建一个 axios 实例
const service = axios.create({
  baseURL: MockUrl,
  // baseURL: process.env.API + '/custom/owens/',
  timeout: 5000 // 请求超时时间
})

// 请求拦截器
service.interceptors.request.use(
  config => {
    // const storeId = store.state.common.act_id
    let url = new URL(location.href)
    // debugger
    // let ls = url.searchParams.get('ls')
    let actId = url.searchParams.get('act_id')
    // window.alert(actId + 'actId')
    console.log(actId, 'actId')
    // 在请求发送之前做一些处理
    const token = getCookies('gameToken')
    // 让每个请求携带token-- ['X-Token']为自定义key 请根据实际情况自行修改
    // config.headers['X-Token'] = toke
    config.headers['Authorization'] = `Bearer ${token}`
    config.headers['Accept'] = 'application/vnd.fyplatform.v1+json'
    config.headers['X-ACT-ID'] = actId || 710
    return config
  },
  error => {
    // 发送失败
    console.log(error)
    return Promise.reject(error)
  }
)

// 响应拦截器
service.interceptors.response.use(
  response => {
    // dataAxios 是 axios 返回数据中的 data
    const dataAxios = response.data
    // 这个状态码是和后端约定的
    const { code } = dataAxios
    // 根据 code 进行判断
    if (code === undefined) {
      // 如果没有 code 代表这不是项目后端开发的接口 比如可能是 D2Admin 请求最新版本
      return dataAxios
    } else {
      // 有 code 代表这是一个后端接口 可以进行进一步的判断
      switch (code) {
        case 0:
          // [ 示例 ] code === 0 代表没有错误
          return dataAxios.data
        case 'xxx':
          // [ 示例 ] 其它和后台约定的 code
          console.log('[ code: xxx ]')
          // errorCreate(`[ code: xxx ] ${dataAxios.msg}: ${response.config.url}`)
          break
        default:
          // 不是正确的 code
          console.log('不是正确的 code')
          // errorCreate(`${dataAxios.msg}: ${response.config.url}`)
          break
      }
    }
  },
  async error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          // return error
          // error.message = '请求错误';
          break
        case 401:

          //  error.message = '未授权，请登录'
          break
        case 403:
          error.message = '拒绝访问'
          break
        case 404: error.message = `请求地址出错: ${error.response.config.url}`; break
        case 408: error.message = '请求超时'; break
        case 500: error.message = '服务器内部错误'; break
        case 501: error.message = '服务未实现'; break
        case 502: error.message = '网关错误'; break
        case 503: error.message = '服务不可用'; break
        case 504: error.message = '网关超时'; break
        case 505: error.message = 'HTTP版本不受支持'; break
        default: break
      }
    }
    // errorLog(error)
    return Promise.resolve(error)
    // return Promise.reject(error)
  }
)

export default service
